	import comm from '../../static/js/common.js';
	export default {
		data() {
			return {
				titleh: '全科助理培训管理平台',
				iconh: '&#xe600;',
				tips: '',
				userInfo: {}
			}
		},
		components: {
            xHeader
        },
		onNavigationBarButtonTap(e) {
			this.tips = e
		},
		onLoad() {
			let that = this;
			let _token = uni.getStorageSync('token');
			if(_token == '') {
				uni.navigateTo({
					url: '/pages/login/login'
				})
			}else{
				comm.post(comm.host_gp + "/api/f/user/get").then(function(res) {
					res = res.data;
					that.userInfo = res;
				})
			}
		},
		methods: {
			goItemPage(item) {
				if(item == 'bg') {
					uni.showActionSheet({
						itemList: ['工作督导', '教学文档管理', '设备管理'],
						success: function (res) {
							if(res.tapIndex + 1 == 1) {
								uni.navigateTo({
									url: '/pages/workReview/workReview'
								})
							}
							if(res.tapIndex + 1 == 2) {
								uni.navigateTo({
									url: '/pages/teachingDoc/teachingDoc'
								})
							}
						},
						fail: function (res) {
							console.log(res.errMsg);
						}
					});
				}
				if(item == 'kq') {
					uni.showActionSheet({
						itemList: ['考勤查询', '考勤审批', '考勤违纪', '我的考勤', '我的请假'],
						success: function (res) {
							if(res.tapIndex + 1 == 1) {
								uni.navigateTo({
									url: '/pages/checkWork/checkWork'
								})
							}
							if(res.tapIndex + 1 == 2) {
								uni.navigateTo({
									url: '/pages/approval/approval'
								})
							}
						},
						fail: function (res) {
							console.log(res.errMsg);
						}
					});
				}
